/* components */
.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: var(--primary-color);
  z-index: 501;
  overflow: hidden;
}
.loader-dot {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  margin: 0 5px;
  background-color: var(--white-color);
}
.loader-dot--left {
  animation: loader 1s ease-in infinite;
}
.loader-dot--middle {
  animation: loader 1s 0.3s ease-in infinite;
}
.loader-dot--right {
  animation: loader 1s 0.7s ease-in infinite;
}

@keyframes loader {
  0% {
    transform: scale(0.2);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.2);
  }
}
body:not(.loading) .loader {
  animation: fadeOut 0.5s 0.3s ease-out forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  background-color: var(--header-bg-color);
  z-index: 99;
}
.header__wrapper {
  align-items: center;
}
.header--main .header__logo svg path:not(:first-child):not(:nth-child(2)) {
  display: none;
}
.header__logo {
  margin-right: auto;
  z-index: 99;
}
.header__logo svg {
  width: 221px;
  height: 46px;
}
@media (max-width: 768px) {
  .header__logo svg {
    height: 43px;
    width: 190px;
  }
}
@media (max-width: 534px) {
  .header__logo svg {
    height: 36px;
  }
}

.burger {
  margin-left: auto;
